@charset "UTF-8";
p,
h1,
h2,
h3 {
  margin: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: 'Georgia', serif;
  font-weight: 400;
  font-size: 14px;
  color: #ffffff; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

header {
  border-top: 5px solid #76c38f;
  background-color: #290000;
  padding-top: 41px;
  padding-bottom: 47px; }

main {
  background-color: #660000; }

.container {
  width: 960px;
  margin: 0 auto; }

.link {
  display: block;
  padding: 10px 20px;
  font-size: 13px;
  line-height: 16px;
  border-radius: 10px;
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
  transition: background-color 0.3s; }
  .link:hover, .link:focus {
    background-color: #76c38f; }

.nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 37px; }

.nav__logo {
  margin-right: 50px; }
  .nav__logo p {
    font-size: 28px;
    line-height: 28px;
    margin-bottom: -5px; }
  .nav__logo small {
    font-size: 11px;
    line-height: 11px;
    letter-spacing: 0.6px;
    font-family: inherit;
    color: #979797; }

.nav__list {
  display: flex; }

.presentation {
  display: flex;
  justify-content: space-between; }

.presentation__content {
  margin-top: 97px;
  margin-right: 79px; }
  .presentation__content h3 {
    margin-bottom: 20px;
    font-size: 46px;
    line-height: 46px;
    font-weight: 400; }
  .presentation__content p {
    margin-bottom: 20px;
    font-size: 12px;
    line-height: 19px;
    letter-spacing: 0.4px;
    color: rgba(255, 255, 255, 0.5); }

.presentation__img {
  width: 436px;
  height: 436px;
  flex-shrink: 0; }
  .presentation__img div {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 26px;
    line-height: 31;
    background-color: #333333;
    border-radius: 50%;
    border: 25px solid rgba(255, 255, 255, 0.5); }

.presentation__link {
  position: relative;
  display: inline-flex;
  padding: 10px 16px;
  font-size: 13px;
  line-height: 16px;
  text-transform: none; }
  .presentation__link::after {
    content: "»";
    margin-left: 5px; }

.slider {
  position: relative;
  padding: 0 60px; }

.slider__container {
  overflow: hidden; }

.slider__list {
  width: 100%;
  display: flex; }

.slider__item {
  width: 100px;
  flex-shrink: 0; }

.slider__btn {
  position: absolute;
  top: 50%;
  width: 40px;
  height: 40px;
  transform: translateY(-50%);
  border: none;
  cursor: pointer;
  transition: opacity 0.3s; }
  .slider__btn--forward {
    right: -10px;
    background: transparent url("../img/arrow-forward.png") 50%/15px 28px no-repeat; }
  .slider__btn--back {
    left: -15px;
    background: transparent url("../img/arrow-back.png") 50%/15px 28px no-repeat; }
  .slider__btn:hover, .slider__btn:focus {
    opacity: 0.5; }

.service {
  padding-top: 60px;
  padding-bottom: 100px; }

.service__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(225px, 1fr));
  gap: 20px;
  width: 100%;
  flex-wrap: wrap; }
  .service__list li {
    opacity: 0; }

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 30px;
  text-align: center;
  background-color: #ffffff;
  transition-property: background-color, color;
  transition-delay: 0.15s;
  color: #333333;
  text-decoration: none; }
  .card:hover, .card:focus {
    color: #ffffff;
    background-color: #76c38f; }
    .card:hover .card__img, .card:focus .card__img {
      background-color: #ffffff;
      color: #333333; }
    .card:hover p, .card:focus p {
      opacity: 1; }
  .card h3 {
    font-size: 32px;
    line-height: 32px;
    margin-bottom: 16px;
    font-weight: 400; }
  .card p {
    font-size: 12px;
    line-height: 17px;
    opacity: 0.5;
    transition: opacity 0.3s; }

.card__img {
  width: 120px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 14px;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  background-color: #333333;
  transition-property: background-color, color;
  transition-delay: 0.15s; }

.fade-in {
  animation-name: amin-card;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out; }

@keyframes amin-card {
  from {
    transform: translate3d(0, 50px, 0);
    opacity: 0; }
  to {
    transform: translate3d(0, 0, 0);
    opacity: 1; } }
